import { Component, Injector, OnInit } from '@angular/core';
import {
  AbstractControl,
  ValidationErrors,
  ValidatorFn,
  Validators,
} from '@angular/forms';
import { PerfilService } from 'app/modules/perfil/api/perfil.service';
import { Perfil } from 'app/modules/perfil/models/perfil.model';
import { BaseResourceFormComponent } from 'app/shared/containers/base-resource-form';
import { IFilter } from 'app/shared/models/interface.model';
import { UsuarioService } from '../../api/usuario.service';
import { Usuario, UsuarioResponse } from '../../model/usuario.model';
import toastr from 'toastr';

@Component({
  selector: 'app-usuario-form',
  templateUrl: './usuario-form.component.html',
  styleUrls: ['./usuario-form.component.scss'],
})
export class UsuarioFormComponent
  extends BaseResourceFormComponent<Usuario, UsuarioResponse>
  implements OnInit
{
  status: number = 0;

  perfils: Perfil[] = [];

  constructor(
    protected usuarioService: UsuarioService,
    protected injector: Injector,
    protected perfilService: PerfilService
  ) {
    super(injector, new Usuario(), usuarioService);
  }

  ngOnInit(): void {
    super.ngOnInit();
    setTimeout(() => {
      this.carregaPerfils();
    }, 500);
  }

  protected buildResourceForm() {
    this.resourceForm = this.formBuilder.group({
      nome: [null, [Validators.required, Validators.minLength(2)]],
      email: [null, [Validators.required, Validators.email]],
      cpf: [null, [Validators.required, Validators.minLength(11)]],
      login: [null, [Validators.required]],
      dataNascimento: [null, [Validators.required]],
      status: [null, [Validators.required]],
      perfilId: [null, [Validators.required]],
      senha: [null, [Validators.minLength(6)]],
      senhaConfirmacao: [
        null,
        [Validators.minLength(6), this.matchValidator('senha')],
      ],
    });
  }

  protected carregaPerfils() {
    const filtro: IFilter = {
      filtro: '',
      pagina: 0,
      direcaoOrdenacao: 'DESC',
      tamanho: 100,
      ordenarPor: 'id',
    };

    this.perfilService.getAll(filtro).subscribe(
      (perfils) => {
        this.perfils = perfils.content;
        this.resourceForm.patchValue({
          perfilId: this.resource?.perfil?.id,
        });
      },
      (err) =>
        toastr.error('Não foi possível carregar os perfils', err.error.error, {
          timeOut: 10000,
          progressBar: true,
          closeButton: true,
        })
    );
  }

  protected createPageTitle(): string {
    return 'Cadastro de Novo Usuário';
  }

  protected editionPageTitle(): string {
    const usuarioNome = this.resource.nome || '';
    return 'Editando usuário: ' + usuarioNome;
  }

  protected matchValidator(matchTo: string, reverse?: boolean): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      if (control.parent && reverse) {
        const c = (control.parent?.controls as any)[matchTo] as AbstractControl;
        if (c) {
          c.updateValueAndValidity();
        }
        return null;
      }
      return !!control.parent &&
        !!control.parent.value &&
        control.value === (control.parent?.controls as any)[matchTo].value
        ? null
        : { matching: true };
    };
  }
}
